/* CSS VARIABLES */

/* COLORS */
:root {
  /* LIGHT THEME */
  --primary-color: #eb5e28;
  --primary-color-tint--30: #f18e69;
  --primary-color-tint--60: #f7bfa9;
  --primary-color-tint--90: #fdefea;
  --primary-color-shade--30: #a5421c;
  --primary-color-shade--60: #5e2610;
  --primary-color-shade--90: #170904;

  /* --secondary-color: #66635d;
  --secondary-color-tint--30: #94928e;
  --secondary-color-tint--60: #c2c1be;
  --secondary-color-tint--90: #f0efef;
  --secondary-color-shade--30: #474541;
  --secondary-color-shade--60: #292825;
  --secondary-color-shade--90: #0a0a09; */

  --background-color: #fcf7f1;
  --font-color: #252422;
  --font-color--secondary: #403d39;

  /* MAIN NAVIGATION */
  /* rgb(255,255,255) */
  --nav-background-color: 255, 255, 255;

  /* INTERACTION COLOR */
  --interaction-background-color: #e6dfd3;
  --interaction-background-color-hw: #fff;
}

.dark-theme {
  --primary-color: #0085ff;
  --primary-color-tint--30: #4daaff;
  --primary-color-tint--60: #99ceff;
  --primary-color-tint--90: #e6f3ff;
  --primary-color-shade--30: #005db3;
  --primary-color-shade--60: #003566;
  --primary-color-shade--90: #000d19;

  /* --secondary-color: #001f3b;
  --secondary-color-tint--30: #4d6276;
  --secondary-color-tint--60: #99a5b1;
  --secondary-color-tint--90: #e6e9eb;
  --secondary-color-shade--30: #001629;
  --secondary-color-shade--60: #000c18;
  --secondary-color-shade--90: #000306; */

  --background-color: #031423;
  --font-color: #d6def8;
  --font-color--secondary: #969bae;

  /* MAIN NAVIGATION */
  /* rgb(27,55,80) */
  --nav-background-color: 27, 55, 80;

  /* INTERACTION COLOR */
  --interaction-background-color: #002c55;
  --interaction-background-color-hw: #1b3750;

  transition: all 0.3s ease-in-out;
}

/* TYPOGRAPHY */
:root {
  --body-font: 'Nunito', sans-serif;
  --font-size-normal-percentage: 62.5%;
  --font-size-small-percentage: 56.25%;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  /* PERCENTAGE OF USER'S BROWSER FONT-SIZE SETTING */
  font-size: 62.5%;
  font-size: var(--font-size-normal-percentage);
}

body {
  font-family: 'Nunito', sans-serif;
  font-family: var(--body-font);
  color: #252422;
  color: var(--font-color);

  display: block;
  background: #fcf7f1;
  background: var(--background-color);
}

input,
textarea,
button {
  font-family: inherit;
}

/* RESPONSIVENESS */
@media (max-width: 64em) {
  html {
    font-size: 56.25%;
    font-size: var(--font-size-small-percentage);
  }
}

/* HELPER CLASSES */
/* CENTER */
.center-text {
  text-align: center;
}

.center-flex-row {
  display: flex;
  justify-content: center;
  align-items: center;
}

.center-flex-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* GAPS */
.extra-tiny-gap {
  grid-gap: 0.4rem;
  gap: 0.4rem;
}
.tiny-gap {
  grid-gap: 0.8rem;
  gap: 0.8rem;
}
.extra-small-gap {
  grid-gap: 1.6rem;
  gap: 1.6rem;
}
.small-gap {
  grid-gap: 2.4rem;
  gap: 2.4rem;
}
.medium-gap {
  grid-gap: 4.8rem;
  gap: 4.8rem;
}
.large-gap {
  grid-gap: 9.6rem;
  gap: 9.6rem;
}
/* BELOW IS FOR SIMILAR VIEW IN DRAWING/PHOTO/POST PAGES THE SAME AS PORTFOLIO PAGE */
.container-gap {
  height: 4vh;
}

/* RESPONSIVE WIDTH */
.responsive-width {
  width: 50vw;
  max-width: 1000px;
}

@media (max-width: 64em) {
  .responsive-width {
    width: 70vw;
  }
}

@media (max-width: 32em) {
  .responsive-width {
    width: 90vw;
  }
}

/* CSS TRANSITIONS */
.slide-in-top-enter {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  opacity: 0;
}

.slide-in-top-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

.slide-in-top-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.slide-in-top-exit-active {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.main-header {
  position: fixed;
  z-index: 999;

  height: 8vh;
  width: 100%;

  background-color: rgba(var(--nav-background-color), 0.4);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

.nav-links {
  list-style: none;
}

.nav-links a:link,
.nav-links a:visited {
  text-decoration: none;
  color: var(--font-color);
  font-size: 2rem;
  font-weight: 400;
}

@media (min-width: 768px) {
  .nav-links a:link,
  .nav-links a:visited,
  .nav-links a:hover,
  .nav-links a:active {
    position: relative;
  }

  .nav-links a:link::after,
  .nav-links a:visited:after,
  .nav-links a:hover::after,
  .nav-links a:active::after {
    position: absolute;
    content: '';
    height: 1px;
    top: 90%;
    left: 0;
    background-color: var(--font-color);

    width: 0;
    transition: all 0.3s ease-in-out;
  }

  .nav-links a:hover::after,
  .nav-links a:active::after {
    width: 100%;
  }
}

@media (max-width: 1023px) {
  .nav-links {
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
}

.side-drawer {
  position: fixed;
  top: 8vh;
  right: 0;
  z-index: 99;

  height: 24vh;
  width: 100%;

  background: rgba(var(--nav-background-color), 0.8);
  box-shadow: 0 7px 30px -10px rgba(150, 170, 180, 0.4);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 9;
}

.main-navigation {
  /* OVERRIDE JUSTIFY-CONTENT */
  justify-content: space-between;
}

.main-navigation__title a:link,
.main-navigation__title a:visited,
.main-navigation__title a:hover,
.main-navigation__title a:active {
  text-decoration: none;

  color: var(--font-color);
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: -0.5px;
}

.main-navigation__title img {
  height: 2rem;
}

.main-navigation__menu-btn {
  width: 2rem;
  height: 2rem;
  background: transparent;
  border: none;

  display: flex;
  flex-direction: column;
  justify-content: space-around;

  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 2rem;
  height: 2px;
  background: var(--font-color);
}

.main-navigation__drawer-nav {
  height: 100%;
}

/* SWITCH BETWEEN NAV LINKS & MENU BUTTON */
@media (min-width: 1024px) {
  .main-navigation__menu-btn {
    display: none;
  }
}

@media (max-width: 1023px) {
  .main-navigation__links {
    display: none;
  }
}

/* MAIN NAVIGATION RESPONSIVENESS */
@media (max-width: 64em) {
  .main-navigation {
    width: 70%;
  }
}

@media (max-width: 32em) {
  .main-navigation {
    width: 90%;
  }
}

.footer {
  width: 50%;
  margin: auto;

  font-size: 1.2rem;
  color: var(--font-color);

  /* TODO ADD PADDING */
  padding: 0rem 6.4rem 6.4rem;
}

/* FOOTER RESPONSIVENESS */
@media (max-width: 64em) {
  .footer {
    width: 70%;
  }
}

@media (max-width: 32em) {
  .footer {
    width: 90%;
  }
}

.button {
  display: inline-block;

  border-radius: 0.4rem;
  padding: 0.6rem 1.2rem;

  font-weight: 500;
  font-size: 1.8rem;
  text-decoration: none;

  cursor: pointer;

  transition: all 0.3s;
}

.button--primary {
  background-color: var(--primary-color);
  color: #fff;
  border: 1px solid var(--primary-color);
}

.button--primary:hover,
.button--primary:active,
.button--primary:focus {
  background-color: var(--primary-color-shade--30);
  border-color: var(--primary-color-shade--30);
}

.button--secondary {
  font-weight: 500;
  font-size: 1.8rem;
  color: var(--font-color);

  background-color: transparent;
  border: none;

  padding: 0;
}

.button--danger:hover,
.button--danger:active,
.button--danger:focus {
  color: #fff;
  background: #ff7675;
  border-color: #ff7675;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #fff;
  border-color: #ccc;
  cursor: not-allowed;
}

/* NOTE ADD UNDERLINE WHEN HOVER*/
.button--hoverUnderline:link,
.button--hoverUnderline:visited,
.button--hoverUnderline:hover,
.button--hoverUnderline:active {
  position: relative;
}
.button--hoverUnderline:link::after,
.button--hoverUnderline:visited::after,
.button--hoverUnderline:hover::after,
.button--hoverUnderline:active::after {
  position: absolute;
  content: '';
  height: 1px;
  top: 100%;
  left: 0;
  background-color: var(--font-color);

  width: 0;
  transition: all 0.3s ease-in-out;
}

.button--hoverUnderline:hover::after,
.button--hoverUnderline:active::after {
  width: 100%;
}

.button--small {
  font-size: 1.6rem;
}
/* BUTTON DEFAULT: 1.8rem */
.button--medium {
  font-size: 2rem;
}
.button--large {
  font-size: 2.4rem;
}

.section-hero {
  height: 88vh;

  margin-bottom: 6.4rem;

  position: relative;
}

/* HERO TITLE */
.hero-title {
  height: 36vh;

  justify-content: space-between;
  margin-bottom: 3.2rem;

  color: var(--font-color);
}

.hero-title--text h2 {
  font-size: 4.4rem;
  font-weight: 700;
}

.hero-title--text p {
  font-size: 2rem;
}

.hero-title--image img {
  height: 36vh;

  border-radius: 999rem;

  z-index: -9;
}

@media (max-width: 64em) {
  .hero-title {
    height: 30vh;
  }

  .hero-title--image img {
    height: 24vh;
  }
}

@media (max-width: 32em) {
  .hero-title {
    height: 24vh;
  }

  .hero-title--image img {
    height: 16vh;
  }
}

/* HERO DESCRIPTION */
.hero-description p {
  /* FIXME ALL PARAGRAPH LINE HEIGHT NEED TO BE ADJUSTED SYSTEMMATICALLY */
  line-height: 1.6;

  color: var(--font-color--secondary);
}

@media (max-width: 64em) {
  .hero-title--text h2 {
    font-size: 3.6rem;
  }
}

.hero-description {
  font-size: 1.8rem;
  color: var(--font-color);

  margin-bottom: 3.2rem;

  /* OVERWRITE FLEX CENTER */
  align-items: flex-start;
}

/* HERO CTA */
.hero-cta {
  margin-bottom: 3.2rem;
}

/* TODO SHOULD HEIGHT BE SET TO AUTO? */
@media (max-width: 64em) {
  .section-hero {
    height: auto;
  }
  .hero-cta-scroll {
    display: none;
  }
}

