/* .section-interactive {
} */

.checkbox-interactive {
  font-size: 1.2rem;

  height: 4vh;
}

.interactive-container {
  height: 0;
  opacity: 0;

  transition: all 0.6s ease-in-out;
}

.active {
  height: 88vh;
  opacity: 1;

  margin-bottom: 6.4rem;
}

.interactive-chat {
  height: 84vh;
  /* overflow: hidden; */
  overflow-y: scroll;
  overflow-x: hidden;

  /* IE and Edge */
  -ms-overflow-style: none;
  /* Firefox */
  scrollbar-width: none;

  display: flex;
  flex-direction: column-reverse;

  font-size: 1.6rem;
  color: var(--font-color);
}

/* HIDE VERTICAL SCROLL BAR */
.interactive-chat::-webkit-scrollbar {
  width: 0px;
  /* make scrollbar transparent */
  background: transparent;
}

.interactive-scroll {
  height: 4vh;
  opacity: 0;

  -webkit-animation: delayScroll 0.15s ease-out 0s forwards;

          animation: delayScroll 0.15s ease-out 0s forwards;
  -webkit-animation-delay: 20.3s;
          animation-delay: 20.3s;
}

@-webkit-keyframes delayScroll {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes delayScroll {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.chat {
  display: flex;
  flex-direction: row-reverse;

  /* NOTE SET UP ORIGIN FOR AN ELEMENT'S TRANSFORMATION */
  -webkit-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
  padding-top: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
  max-height: 0;
  overflow: hidden;

  -webkit-animation: message 0.15s ease-out 0s forwards;

          animation: message 0.15s ease-out 0s forwards;
  -webkit-animation-delay: var(--delay);
          animation-delay: var(--delay);

  --bgcolor: var(--interaction-background-color);
  --radius: 0.6rem 0.6rem 0 0.6rem;
}

.chat__howie {
  flex-direction: row;
  /* text-align: right; */
  /* align-self: flex-end; */

  -webkit-transform-origin: 0 100%;

          transform-origin: 0 100%;

  --bgcolor: var(--interaction-background-color-hw);
  --radius: 0.6rem 0.6rem 0.6rem 0;
}

.chat__content {
  padding: 0.6rem 1.2rem;
  /* flex: 0 1 auto; */

  background: var(--bgcolor);
  border-radius: var(--radius);
}

.chat__content a {
  color: var(--primary-color);
  text-decoration: none;
}

.chat__content a:link::after,
.chat__content a:visited::after,
.chat__content a:hover::after,
.chat__content a:active::after {
  background-color: var(--primary-color);
}

@-webkit-keyframes message {
  0% {
    max-height: 100vmax;
  }
  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    max-height: 100vmax;
    overflow: visible;
    /* overflow: scroll; */
    padding-top: 1rem;
  }
}

@keyframes message {
  0% {
    max-height: 100vmax;
  }
  80% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    max-height: 100vmax;
    overflow: visible;
    /* overflow: scroll; */
    padding-top: 1rem;
  }
}

/* VIEW IN GRID */
.project-container--grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 2.4rem;
  gap: 2.4rem;

  margin-bottom: 6.4rem;
}

.project-content-container--grid {
  /* OVERWRITE CENTERED ALIGN ITEMS */
  align-items: flex-start;
}

.project-title--grid {
  font-size: 2rem;
  font-weight: 700;
  color: var(--font-color);
}

.project-image--grid {
  width: 100%;
  height: 18rem;

  display: flex;
  justify-content: center;
  align-items: center;

  overflow: hidden;

  /* FIXME BG COLOR WILL BE UPDATED BASED ON NEW COVER IMAGE */
  /* background-color: #fff; */
  /* outline: 1px solid #ccc5b9; */
  border-radius: 0.6rem;
}

.project-image--grid img {
  width: auto;
  height: 100%;
  object-fit: cover;
}

.project-technology--grid {
  list-style: none;

  font-size: 1.6rem;
  font-weight: 600;
  color: var(--font-color--secondary);
}

.project-technology--grid li {
  color: var(--font-color);
  font-size: 1.2rem;
  font-weight: 500;

  background-color: var(--interaction-background-color);

  padding: 2px 8px;
  border-radius: 4px;
}

.project-description--grid {
  font-size: 1.8rem;
  color: var(--font-color--secondary);

  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 0.8rem;
  gap: 0.8rem;
}

@media (max-width: 32em) {
  .project-sub-container--grid {
    width: 100%;

    flex-direction: column;
    align-items: center;
  }

  .project-image--grid {
    width: 100%;
  }

  .project-technology--grid {
    width: 100%;

    flex-direction: row;
    justify-content: flex-start;
  }
}

/* VIEW IN LIST */
.project-container--list {
  display: flex;
  flex-direction: column;
  grid-gap: 0.4rem;
  gap: 0.4rem;

  padding: 0.4rem 0.8rem;
  margin-bottom: 2.4rem;
}

.project-container--list:hover {
  background-color: var(--interaction-background-color);
  border-radius: 0.6rem;

  transition: all 0.3s;
}

.project-title--list {
  font-size: 2rem;
  font-weight: 700;
  color: var(--font-color);
}

.project-cta--list a {
  font-size: 1.6rem;
  color: var(--font-color--secondary);
  font-weight: 400;
}

.project-cta--list a:hover {
  color: var(--font-color);
}

.project-description--list {
  font-size: 1.6rem;
  color: var(--font-color--secondary);

